import Rushbet from "./rushbet";
import BetHouseClass from "./bethouse";
import { type I_BetHouse } from "./types";

export const BetsService = {
	readBets: function readBets(betHouseName: I_BetHouse["name"], domHTML: string) {
		const BetHouse = new BetHouseClass(betHouseName === "rushbet" ? new Rushbet() : new Rushbet());
		const bets = BetHouse.extractBetsData(BetHouse.parseHTML(domHTML), betHouseName);

		return bets;
	},
};

