import { type T_Bets, type I_BetHouse, type T_MultipleBet, type T_Bet } from "./types";

class BetHouse {
	public betHouse;

	constructor(betHouse: I_BetHouse) {
		this.betHouse = betHouse;
	}

	extractBetsData(document: Document, betHouseName: I_BetHouse["name"]) {
		const bets: T_Bets = [];

		this.betHouse.getBetsElements(document).forEach((betElement) => {
			const betType = this.betHouse.getBetType(betElement);

			if (betType === "Sencilla") {
				const date = this.betHouse.getBetDate(betElement);
				const [teamA, teamB] = this.betHouse.getBetTeams(betElement);
				const [name, details] = this.betHouse.getBetNameAndDetails(betElement, teamA, teamB);
				const quota = this.betHouse.getBetQuota(betType, betElement);
				const stake = this.betHouse.getBetStake(betElement);
				const payment = this.betHouse.getBetPayment(betElement, stake);

				const bet = {
					type: "Sencilla",
					date,
					name,
					details,
					teamA,
					teamB,
					quota,
					stake,
					payment,
				} as const;

				bets.push(bet);
			} else {
				const date = this.betHouse.getBetDate(betElement);
				const quota = this.betHouse.getBetQuota(betType, betElement);
				const stake = this.betHouse.getBetStake(betElement);
				const payment = this.betHouse.getBetPayment(betElement, stake);

				const bet = {
					type: "Combinada" as const,
					date,
					quota,
					stake,
					payment,
					bets: [],
				} as T_MultipleBet;

				this.betHouse.getMultipleBetItemsElements(betElement).forEach((betElement) => {
					const [teamA, teamB] = this.betHouse.getBetTeams(betElement);
					const [name, details] = this.betHouse.getBetNameAndDetails(betElement, teamA, teamB);
					const quota = this.betHouse.getBetItemQuota(betElement);

					bet.bets.push({
						name,
						details,
						teamA,
						teamB,
						quota,
					});
				});

				bets.push(bet);
			}
		});

		return this.toCSV(bets, betHouseName);
	}

	parseHTML(html: string) {
		return this.betHouse.parseHTML(html);
	}

	private toCSV(bets: T_Bet[], betHouseName: I_BetHouse["name"]) {
		bets
			.map((bet, index) => {
				if (bet.type === "Combinada") {
					return bet.bets
						.map((betItem) => {
							return [
								index,
								betItem.teamA,
								betItem.teamB,
								betItem.name,
								betItem.details,
								bet.quota,
								bet.date,
								betHouseName,
								"NO",
								"",
								bet.stake,
								bet.payment,
							].join(";");
						})
						.join("\n");
				}

				return [
					index,
					bet.teamA,
					bet.teamB,
					bet.name,
					bet.details,
					bet.quota,
					bet.date,
					betHouseName,
					"NO",
					"",
					bet.stake,
					bet.payment,
				].join(";");
			})
			.join("\n");
	}
}

export default BetHouse;
